.structure__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    text-align: center;
}

.structure__heading {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    margin-bottom: 50px;
    color: var(--color-primary); /* Customize color as per your theme */
}

.structure__content {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    display: flex;
    margin-top: 30px;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
}

.structure__text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    flex: 1;
    padding-right: 2rem;
    text-align: left;
    line-height: 1.6;
    margin-bottom: 30px;
}

.structure__image {
    flex: 1;
}

.structure__image img {
    max-width: 150%;
    height: auto;
    margin-right: 10px;
    margin-bottom: 30px;
    border-radius: 8px;
}

@media screen and (max-width: 850px) {
    .structure__content {
        flex-direction: column;
    }

    .structure__text {
        padding-right: 0;
        text-align: center;
    }
}
