.structure__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    text-align: center;
}

.structure__heading {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    margin-bottom: 50px;
    color: var(--color-primary); /* Customize color as per your theme */
}

.structure__content {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    display: flex;
    margin-top: 30px;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    align-content: center;
}

.structure__text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    flex: 1;
    padding-right: 2rem;
    text-align: left;
    line-height: 1.6;
    margin-bottom: 30px;
}

.structure__image {
    display: flex; /* This will apply flex properties */
    flex: 1;
    justify-content: center; /* Center-align images horizontally */
    align-items: center; /* Center-align images vertically */
    flex-wrap: wrap; /* Ensure multiple images wrap correctly */
}

.structure__image img {
    max-width: 100%; /* Ensure the images fit within the parent */
    height: auto;
    margin-right: 40px;
    margin-bottom: 30px;
    border-radius: 8px;
}

/* Ensure the last image doesn't have extra right margin */
.structure__image img:last-child {
    margin-right: 0;
}

@media screen and (max-width: 850px) {
    .structure__content {
        flex-direction: column;
    }

    .structure__text {
        padding-right: 0;
        text-align: center;
    }

    .structure__image img {
        margin-right: 0; /* Remove the margin on smaller screens */
        max-width: 80%; /* Reduce the image size for small screens */
    }
}
